import React from 'react';
import '@css/utils/Navbar.scss';
import { Link, useNavigate } from 'react-router-dom';
import { useSetRecoilState } from 'recoil';
import GlobalLoginState from '@recoil/GlobalLoginState';
import { clearToken, getToken } from '@cert/TokenStorage';
import { Dropdown } from './Dropdown';

function Navbar() {
  const setLoginState = useSetRecoilState(GlobalLoginState);
  const navigate = useNavigate();

  const onClickLogOut = () => {
    clearToken();
    setLoginState(() => {
      return {
        id: '',
        isLogin: false,
        isAdmin: false,
        profileUrl: '',
      };
    });
    alert(' !');
    navigate('/auth');
  };

  const onClickAuthTimeline = () => {
    if (getToken()) {
      navigate('/2022-timeline');
    } else {
      alert('  !');
      navigate('/auth');
    }
  };

  const onClickAuthReview = () => {
    if (getToken()) {
      navigate('/review/');
    } else {
      alert('  !');
      navigate('/auth');
    }
  };

  const onClickEventCreate = () => {
    navigate('/');
  };

  const onClickEventMatching = () => {
    navigate('/Result');
  };

  const onClickRotation = () => {
    if (getToken()) {
      navigate('/rotation/');
    } else {
      alert('  !');
      navigate('/auth');
    }
  };

  const onClickCalendar = () => {
    navigate('/rotation/calendar');
  };

  return (
    <div className="navbar--wrapper">
      <Dropdown
        title={<button></button>}
        menu={[
          <button onClick={onClickEventCreate}> </button>,
          <button onClick={onClickEventMatching}> </button>,
        ]}
      />
      <Dropdown
        title={<button> </button>}
        menu={[
          <button onClick={onClickRotation}></button>,
          <button onClick={onClickCalendar}> </button>,
        ]}
      />
      <span onClick={onClickAuthReview}></span>
      <span onClick={onClickAuthTimeline}></span>
      {getToken() ? <span onClick={onClickLogOut}></span> : <Link to={`/auth`}></Link>}
    </div>
  );
}

export default Navbar;
